import { useState } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import {
  Button,
  FormControl,
  IconButton,
  Input,
  InputAdornment,
  InputLabel,
  Box,
  Typography,
  FormHelperText,
  FormGroup,
} from "@mui/material";
import { Visibility, VisibilityOff } from "@mui/icons-material";
import AuthService from "~/services/AuthService";
import styles from "./ResetPassword.module.css";
import { useTranslation } from "react-i18next";
import { PROCESSED_ERRORS } from "~/utils/errorsProcessor";
import { useDispatch } from "react-redux";
import { setLoading } from "~/state/edit/editState";

function ResetPassword() {
  const { t } = useTranslation("manage");
  const [searchParams] = useSearchParams();
  const token = searchParams.get("token");
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const [tokenError, setTokenError] = useState("");
  const [password, setPassword] = useState("");
  const [passError, setPassError] = useState("");
  const [confrimPassword, setConfrimPassword] = useState("");
  const [confrimPasswordError, setConfrimPasswordError] = useState("");

  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword((show) => !show);

  const validate = () => {
    setConfrimPasswordError("");
    setPassError("");
    if (password.length == 0) {
      setPassError(t("password_required"));
      return false;
    }
    if (confrimPassword.length == 0) {
      setConfrimPasswordError(t("confirm_password_required"));
      return false;
    }

    if (confrimPassword != password) {
      setConfrimPasswordError(t("error.password_should_match"));
      return false;
    }

    return true;
  };

  const onPasswordChanged = (e) => {
    setPassword(e.target.value);
    setPassError("");
  };

  const onConfirmPasswordChanges = (e) => {
    setConfrimPassword(e.target.value);
    setConfrimPasswordError("");
  };

  const onSubmit = () => {
    if (!validate()) {
      return;
    }
    dispatch(setLoading(true));
    AuthService.resetPassword(token, password)
      .then((data) => {
        navigate("/");
      })
      .catch((e) => {
        onApiError({
          error: e,
          globalErrorHandler: (processedError) => {
            dispatch(onError(processedError));
          },
          locallErrorHandler: (processedError) => {
            if (
              processedError.name == PROCESSED_ERRORS.WRONG_RESET_TOKEN.name ||
              processedError.name == PROCESSED_ERRORS.EXPIRED_RESET_TOKEN.name
            ) {
              setTokenError(t(`processed_errors.${processedError.name}`));
            }
          },
        });
      })
      .finally(() => {
        dispatch(setLoading(false));
      });
  };

  return (
    <Box className={styles.wrapper}>
      <Box className={styles.content}>
        <Typography variant="h4">{t("reset_password.title")}</Typography>
        <Box className={styles.form}>
          <FormGroup>
            <FormControl required={true} variant="standard" sx={{ mt: "10px" }}>
              <InputLabel htmlFor="standard-adornment-password">
                {t("label.new_password")}
              </InputLabel>
              <Input
                error={passError.length > 0}
                onChange={onPasswordChanged}
                name="password"
                value={password}
                id="standard-adornment-password"
                type={showPassword ? "text" : "password"}
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                    >
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                }
              />
            </FormControl>
            <FormControl required={true} variant="standard" sx={{ mt: "10px" }}>
              <InputLabel htmlFor="standard-adornment-password">
                {t("label.confirm_new_password")}
              </InputLabel>
              <Input
                error={confrimPasswordError.length > 0}
                onChange={onConfirmPasswordChanges}
                name="cpassword"
                value={confrimPassword}
                id="standard-adornment-password"
                type={showPassword ? "text" : "password"}
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                    >
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                }
              />
            </FormControl>
            {tokenError && (
              <FormHelperText className={styles.errorText}>
                {tokenError}
              </FormHelperText>
            )}
            {(passError || confrimPasswordError) && (
              <FormHelperText className={styles.errorText}>
                {passError || confrimPasswordError}
              </FormHelperText>
            )}
          </FormGroup>
          <Button onClick={onSubmit} sx={{ mt: "40px" }}>
            {t("reset_password.reset")}
          </Button>
        </Box>
      </Box>
    </Box>
  );
}

export default ResetPassword;
